@charset "utf-8";

// Content settings
$content-width:    1080px !default;
$on-palm:          600px !default;
$on-laptop:        900px !default;
$site-top-margin:  12px !default;
$spacing-unit:     36px !default;

$table-text-align: left !default;
$standard-alignment: left;

// Color scheme
$text-color:       #2d2d2d !default;
$background-color: hsl(0, 0%, 99%) !default;
$footer-color:     hsl(0, 0%, 97%);
$brand-color:      #6903EB !default;
$brand-color-secondary:      #9964DC !default;
$grey-color:       #828282 !default;
$grey-color-light: lighten($grey-color, 40%) !default;
$grey-color-dark:  darken($grey-color, 25%) !default;
$black-color:      #333333 !default;

// Font families
$base-font-family: "Quicksand", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol" !default;
$secondary-font-family: "Quicksand", Arial, sans-serif;
$display-font-family: "Quicksand", sans-serif;
$logo-font-family: "Rubik", sans-serif;
$code-font-family: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace !default;

// Font sizes
$base-font-size:   18px !default;
$base-font-weight: 500 !default;
$base-line-height: 1.45 !default;
$small-font-size:  $base-font-size * 5 / 6 !default;
$body-letter-spacing: 0.15px;

// Import partials.
@import
  "minima/base",
  "minima/layout",
  "minima/syntax-noir"
;

// Import Google Fonts
@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;0,400;0,700;0,900;1,300;1,400;1,700;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;700;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;600&display=swap');
