/**
 * Site header
 */
.site-header {
  border-top: 0px solid $grey-color-dark;
  border-bottom: 0px solid $grey-color-light;
  min-height: $spacing-unit * 1.865;

  // Positioning context for the mobile navigation icon
  position: relative;
}

.site-title {
  @include relative-font-size(1.4);
  font-family: $logo-font-family;
  line-height: $base-line-height * $base-font-size * 2.00;
  font-weight: 700;
  letter-spacing: -0.1px;
  text-rendering:geometricPrecision;

  margin-bottom: 0px;
  margin-top: $site-top-margin;
  float: left;
}

#site-logo {
  max-width: 80%;
  &, &:visited {
    color: #555555;
  }
  &:hover {
    color: #666666;
    text-decoration: none;
  }
}

.site-nav {
  float: right;
  line-height: $base-line-height * $base-font-size * 2.25;
  margin-top: $site-top-margin;

  .nav-trigger {
      display: none;
  }

  .menu-icon {
    display: none;
  }

  .page-link {
    color: $text-color;
    line-height: $base-line-height;

    // Gaps between nav items, but not on the last one
    &:not(:last-child) {
      margin-right: 20px;
    }
  }

  @include media-query($on-palm) {
    position: absolute;
    top: 9px;
    right: $spacing-unit / 2;
    background-color: $background-color;
    border: 1px solid $grey-color-light;
    border-radius: 5px;
    text-align: right;

    label[for="nav-trigger"] {
      display: block;
      float: right;
      width: 36px;
      height: 36px;
      z-index: 2;
      cursor: pointer;
    }

    .menu-icon {
      display: block;
      float: right;
      width: 36px;
      height: 26px;
      line-height: 0;
      padding-top: 10px;
      text-align: center;

      > svg {
        fill: $grey-color-dark;
      }
    }

    input ~ .trigger {
      clear: both;
      display: none;
    }

    input:checked ~ .trigger {
      display: block;
      padding-bottom: 5px;
    }

    .page-link {
      display: block;
      padding: 5px 10px;

      &:not(:last-child) {
        margin-right: 0;
      }
      margin-left: 20px;
    }
  }
}



/**
 * Site footer
 */
.site-footer {
  border-top: 1px solid $grey-color-light;
  padding: $spacing-unit 0;
  background-color: $footer-color;
}

.footer-heading {
  @include relative-font-size(1.125);
  margin-bottom: $spacing-unit / 2;
}

.contact-list,
.social-media-list {
  list-style: none;
  margin-left: 0;
}

.footer-col-wrapper {
  @include relative-font-size(0.9375);
  color: $grey-color;
  margin-left: -$spacing-unit / 2;
  @extend %clearfix;
}

.footer-col {
  float: left;
  margin-bottom: $spacing-unit / 2;
  padding-left: $spacing-unit / 2;
}

.footer-col-1 {
  width: -webkit-calc(35% - (#{$spacing-unit} / 2));
  width:         calc(35% - (#{$spacing-unit} / 2));
}

.footer-col-2 {
  width: -webkit-calc(20% - (#{$spacing-unit} / 2));
  width:         calc(20% - (#{$spacing-unit} / 2));
}

.footer-col-3 {
  width: -webkit-calc(45% - (#{$spacing-unit} / 2));
  width:         calc(45% - (#{$spacing-unit} / 2));
}

@include media-query($on-laptop) {
  .footer-col-1,
  .footer-col-2 {
    width: -webkit-calc(50% - (#{$spacing-unit} / 2));
    width:         calc(50% - (#{$spacing-unit} / 2));
  }

  .footer-col-3 {
    width: -webkit-calc(100% - (#{$spacing-unit} / 2));
    width:         calc(100% - (#{$spacing-unit} / 2));
  }
}

@include media-query($on-palm) {
  .footer-col {
    float: none;
    width: -webkit-calc(100% - (#{$spacing-unit} / 2));
    width:         calc(100% - (#{$spacing-unit} / 2));
  }
}



/**
 * Page content
 */
.page-content {
  padding: ($spacing-unit *2) 0;
  flex: 1;
}

.page-heading {
  @include relative-font-size(2);
}

.post-list-heading {
  @include relative-font-size(1.75);
  margin-bottom: revert;
}

.post-list {
  margin-left: 0;
  list-style: none;

  > li {
    margin-bottom: $spacing-unit;
  }
}

.meta-date {
  font-size: $small-font-size;
  font-family: $base-font-family;
  color: $grey-color;
  padding: $spacing-unit/4 0px;
  margin-top:    $spacing-unit/2;
  margin-bottom: $spacing-unit/2;
}

.bottom-border {
  border-bottom: 2px solid $grey-color-light;
}

.post-link {
  display: block;
  font-family: $base-font-family;
  @include relative-font-size(1.4);
}

/**
 * Posts
 */
.post-header {
  margin-top:    $spacing-unit/2;
  margin-bottom: $spacing-unit/2;
}

/*
hr.light {
  margin-bottom: $spacing-unit;
}
*/

.post-title {
  @include relative-font-size(2);
  font-family: $display-font-family;

  font-weight: 400;
  letter-spacing: âˆ’0.1px;
  line-height: 1.2;

  color: $grey-color-dark;
  text-rendering: geometricPrecision;
  font-kerning: normal;

  @include media-query($on-laptop) {
    @include relative-font-size(2.25);
  }
}

.post-intro {
  font-family: $secondary-font-family;
  color: #7d7d7d;
  font-weight: 500;
  width: 480px;


  @include relative-font-size(1.4);   /*font-size: 25px;*/

  line-height: $spacing-unit;
  margin-top: $spacing-unit/2;


  h2 {
    @include relative-font-size(2);
  }
}


.post-content {
  margin-bottom: $spacing-unit;

  padding: $spacing-unit/4 0px;
  margin-bottom: $spacing-unit;

  h2 {
    @include relative-font-size(2);
    @include media-query($on-laptop) {
      @include relative-font-size(1.75);
    }
  }

  h3 {
    @include relative-font-size(1.625);

    @include media-query($on-laptop) {
      @include relative-font-size(1.375);
    }
  }

  h4 {
    @include relative-font-size(1.25);

    @include media-query($on-laptop) {
      @include relative-font-size(1.125);
    }
  }
}

.container {
  width: 100%;
}


.grid {
 display: flex;
 flex-direction: row;
}

.col-1 {
 flex: 1;
}
.col-2 {
 flex: 2;
}
.col-3 {
 flex: 3;
}
.col-4 {
 flex: 4;
}
.col-5 {
 flex: 5;
}
.col-6 {
 flex: 6;
}
.col-8 {
 flex: 8;
}

/* Element Styles */

.ease-in {
  transition-duration: 350ms;
  transition-property: opacity,transform;
  transition-timing-function: cubic-bezier(0.4,0,0.2,1);
}

.col-max {
  //max-width: 40em;
  //margin: auto;
}

.justify-left {
  text-align: left;
}

.justify-right {
  text-align: right;
}

.post-title-box {
  display: block;
  border-left: 0px solid $grey-color-dark;
  padding-left: 0;
}

.grid-element {
  display:block;
  margin: auto;

  float: left;
  margin-right: 10px;
}

.meta-category {
  display: inline-block;
  font-style: normal;
  font-weight: 600;
  letter-spacing: 1.0px;

  @include relative-font-size(0.75);
  line-height: 18px;

  text-transform: uppercase;
  margin-bottom: $base-font-size / 2;
}
